* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e3e3db;
}

a, p {
  color: #1f1f1f;
  letter-spacing: -.021em;
  font-family: Suisse Intl;
  font-size: 15px;
  font-weight: 600;
  line-height: 1;
  text-decoration: none;
}

img {
  object-fit: cover;
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
}

nav, footer {
  z-index: 2;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 2em;
  display: flex;
  position: absolute;
  left: 0;
}

nav {
  top: 0;
}

footer {
  bottom: 0;
}

.container {
  width: 100vw;
  height: 100svh;
  position: relative;
  overflow: hidden;
}

.gallery-container {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  perspective: 2000px;
  will-change: transform;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.gallery {
  transform-origin: center;
  will-change: transform;
  justify-content: center;
  align-items: center;
  width: 600px;
  height: 600px;
  display: flex;
  position: relative;
}

.card {
  transform-origin: center;
  will-change: transform;
  width: 45px;
  height: 60px;
  transform-style: preserve-3d;
  backface-visibility: visible;
  border-radius: 4px;
  position: absolute;
  overflow: hidden;
}

.title-container {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  width: 100%;
  height: 42px;
  position: fixed;
  bottom: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.title-container p {
  text-align: center;
  letter-spacing: -.05rem;
  width: 100%;
  font-size: 36px;
  position: absolute;
}

.word {
  will-change: transform;
  display: inline-block;
  position: relative;
}
