
*{
margin: 0;
padding: 0;
box-sizing: border-box;
}


body {
    background-color: #e3e3db;
}

a, p {

text-decoration: none;
color: #1f1f1f;
font-family: "Suisse Intl";
font-size: 15px;
font-weight: 600;
line-height: 1;
letter-spacing: -0.021em;
}


img {
width: 100%;
height: 100%;
object-fit: cover;
backface-visibility: hidden;
}

nav,
footer {
position: absolute;
left: 0;
width: 100vw;
padding: 2em;
display: flex;
justify-content: space-between;
align-items: center;
z-index: 2;
}

nav {
top: 0;
}

footer {
bottom: 0;
}


.container {

position: relative;
width: 100vw;
height: 100svh;
overflow: hidden;}


.gallery-container {
position: relative;
width: 100%;
height: 100%;
display: flex;
justify-content: center;
align-items: center;
transform-style: preserve-3d;
perspective: 2000px;
will-change: transform;
}


.gallery {
position: relative;
width: 600px;
height: 600px;
display: flex;
justify-content: center;
align-items: center;
transform-origin: center;
will-change: transform;
}

.card {
position: absolute;
width: 45px;
height: 60px;
border-radius: 4px;
transform-origin: center;
will-change: transform;
transform-style: preserve-3d;
backface-visibility: visible;
overflow: hidden;
}

.title-container {
position: fixed;
bottom: 25%;
left: 50%;
transform: translate(-50%, -50%);
width: 100%;
height: 42px;
clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);}


.title-container p {
position: absolute;
width: 100%;
text-align: center;
font-size: 36px;
letter-spacing: -0.05rem;
}


.word {
position: relative;
display: inline-block;
will-change: transform;}